import styles from './dashboard.module.scss';

export const Dashboard = () => {
    return (
        <div className={styles.dashboard}>
            <div className={styles.dashboard__greeting}>
                <p>, User!</p>
                <p>     :</p>
            </div>
            <div className={styles.dashboard__stat}>
                <div>
                    <p className={styles.dashboard__stat_heading}>  :</p>

                    <div className={styles.dashboard__stat_item}>
                        <div className={styles.dashboard__stat_icon}>
                            <i className="fa-solid fa-phone"></i>
                        </div>
                        <div className={styles.dashboard__stat_text}>
                            <span>94</span>
                            <span></span>
                        </div>
                    </div>

                    <div className={styles.dashboard__stat_item}>
                        <div className={styles.dashboard__stat_icon}>
                            <i className="fa-regular fa-clock"></i>
                        </div>
                        <div className={styles.dashboard__stat_text}>
                            <span>94</span>
                            <span></span>
                        </div>
                    </div>
                </div>

                <div>
                    <p className={styles.dashboard__stat_heading}> :</p>

                    <div className={styles.dashboard__stat_item}>
                        <div className={styles.dashboard__stat_icon}>
                            <i className="fa-solid fa-phone"></i>
                        </div>
                        <div className={styles.dashboard__stat_text}>
                            <span>94</span>
                            <span></span>
                        </div>
                    </div>

                    <div className={styles.dashboard__stat_item}>
                        <div className={styles.dashboard__stat_icon}>
                            <i className="fa-regular fa-clock"></i>
                        </div>
                        <div className={styles.dashboard__stat_text}>
                            <span>94</span>
                            <span></span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}